import React from 'react';
import PropTypes from 'prop-types';
import withStyles from 'isomorphic-style-loader/lib/withStyles';
import { bindActionCreators } from 'redux'
import { connect } from 'react-redux'

import {
  setSqueezeLevel
} from '../../actions/ui'

// external-global styles must be imported in your JS.
import normalizeCss from 'normalize.css';
import s from './Layout.css';

import darkBaseTheme from 'material-ui/styles/baseThemes/darkBaseTheme';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import getMuiTheme from 'material-ui/styles/getMuiTheme';
import { grey900, green800 } from 'material-ui/styles/colors';

const muiTheme = getMuiTheme(darkBaseTheme);

class Layout extends React.Component {
  static propTypes = {
    children: PropTypes.node.isRequired,
  };

  componentDidMount() {
    const setSqueeze = () => {
      var w = window.innerWidth
            || document.documentElement.clientWidth
            || document.body.clientWidth;
      this.props.setSqueezeLevel(w)
    }
    window.onresize = setSqueeze
    setSqueeze()
  }

  render () {
    return (
      <MuiThemeProvider muiTheme={muiTheme}>
        <div>
          {this.props.children}
        </div>
      </MuiThemeProvider>
    );
  }
}

const mapDispatchToProps = dispatch =>
  bindActionCreators({ setSqueezeLevel }, dispatch)

export default connect(null, mapDispatchToProps)(withStyles(normalizeCss, s)(Layout))




